.mainBlock {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-color: beige;
    height: 190px;
}

.contentBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    border: 2px solid;
}
.block{
    display: flex;
    justify-content: space-around;
    border: 2px solid;
    width: 80%;
    height: 40px;
}

.childrenBlock{
    background-color: #61dafb;
    border: 2px solid;
    width: 20%;
    height: 90%;
}